/* eslint-disable class-methods-use-this */
import axios, { AxiosRequestConfig } from 'axios';
import { HttpClient as IHttpClient, HttpClientRequestParameters } from '../interfaces/http-client';

export class HttpClient implements IHttpClient {
  get<T, R>(parameters: HttpClientRequestParameters<T>): Promise<R> {
    const { url } = parameters;
    const options: AxiosRequestConfig = {
      headers: {},
    };
    return axios.get(url, options);
  }

  async post<T, R>(parameters: HttpClientRequestParameters<T>): Promise<R> {
    const { url, payload } = parameters;
    const options: AxiosRequestConfig = {
      headers: {},
    };
    return axios.post<T, R>(url, payload, options);
  }
}

const httpClient = new HttpClient();

export { httpClient };
