import { StaffRegisterDTO, StaffRegisterResponseDTO } from 'domain/dto';
import { Plenipotentiary, Staff } from 'domain/entities';
import { StaffRepository as IStaffRepository } from 'domain/registration';
import { StaffRegisterMapper } from 'infrastructure/mappers';
import { httpClient, HttpClient } from 'infrastructure/http-client';

class StaffRepository implements IStaffRepository<HttpClient> {
  httpClient: HttpClient;

  endpoint: string;

  constructor(httpClient: HttpClient, endpoint: string) {
    this.httpClient = httpClient;
    this.endpoint = endpoint;
  }

  async create(newStaff: StaffRegisterDTO) {
    return this.httpClient.post<StaffRegisterDTO, StaffRegisterResponseDTO>({ url: `${this.endpoint}/register`, payload: newStaff });
  }

  async createNewStaff(entities: {
    plenipotentiary: Plenipotentiary,
    staff: Staff,
  }): Promise<StaffRegisterResponseDTO> {
    const { plenipotentiary, staff } = entities;
    const staffRegisterDTO = StaffRegisterMapper.toDTO(plenipotentiary, staff);
    return this.create(staffRegisterDTO);
  }
}

const staffRepository = new StaffRepository(httpClient, '/Staff');

export { staffRepository };
